a{
  text-decoration: none !important;
  color: inherit;
}
button:focus{
  outline-style: none;

}
.element::-webkit-scrollbar{
  width: 0 !important;
}
.viewController{
  width: 100%;
  height: 100%;
  background: #454545;
  position: absolute;
}


.title{
  font-size: 60px;
  margin-left: 3%;
  margin-top: 1%;
  color: white;
  z-index: 1;
}

.recipeDrawer{
  height: 550px;
  width: 400px;
  z-index: 1000;
  border-color: black;
  text-align: right;
  background: #ffb6c1;
  webkit-box-shadow: 0px 0px 59px 5px rgba(0,0,0,0.58);
  -webkit-box-shadow: 0px 0px 59px 5px rgba(0,0,0,0.58);
          box-shadow: 0px 0px 59px 5px rgba(0,0,0,0.58);
  overflow: hidden;
  position: relative;
  margin: auto;
  margin-top: 5%;
  z-index: 1;
}
.recipeView{
  height: 85%;
  overflow: scroll;
}

.recipeIndex{
  text-align: left;
  margin: auto;
  margin-top: 23px;
  padding-left: 3%;
  padding-top: 7px;
  font-size: 35px;
  height: 75px;
  width: 360px;
  color: white;
  border-style: dotted;
  border-color: white;
}

div.recipeIndex:hover{
  background-color: lightblue;
}
.addButton{
  font-size: 25px;
  width: 95px;
  height: 45px;
  background: #0077ee;
  color: white;
  margin-right: 5%;
  margin-bottom: 5%;
  outline: none;
  position: absolute;
  right: 0;
  bottom: 0;
}

.inputPrompt{
  height: 100%;
  width: 400px;
  border-style: dashed;
  border-color: black;
  text-align: right;
  background: white;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
}
.closePrompt{
  position: absolute;
  right: 0;
  font-size: 35px;
  font-weight: bold;
  margin: 0% 3% 0 0;
}
.inputRecipeName{
  height: 15%;
  margin: 5% 5% 5% 5%;
  text-align: left;
}
#recipeName{
  resize:  none;
  border:  none;
  outline: none;
  height: 100%;
  width: 100%;
  font-size: 50px;
  text-align: left;
  border-bottom-style: solid;
  border-color: #0077ee;
  background-color: white;
}

.inputRecipeIngredients{
  height: 55%;
  margin: 5%;
  text-align: left;
  background: white;
}
#recipeIngredients{
  resize:  none;
  border:  none;
  outline: none;
  height: 100%;
  width: 100%;
  font-size: 25px;
  background: white;
}
.saveButton{
  margin-right: 5%;
  margin-top: 10%;
  font-size: 25px;
  width: 95px;
  height: 45px;
  background: #0077ee;
  color: white;
}
.saveEditButton{
  margin-right: 5%;
  margin-top: 10%;
  font-size: 25px;
  width: 95px;
  height: 45px;
  background: #0077ee;
  color: white;
}
.deleteButton{
  margin-right: 5%;
  margin-top: 10%;
  font-size: 25px;
  width: 95px;
  height: 45px;
  background: #0077ee;
  color: white;
}



